.oppurtunity {
  background: var(--high);
  font-family: var(--font-family);

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 8rem 12rem;
}
.oppurtunity_content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex-wrap: wrap;
  flex: 0.7;
}
.oppurtunity_content h1 {
  font-size: 2rem;
  font-weight: 600;
  color: var(--low);
}

.oppurtunity_content p {
  font-size: 1.25rem;
  font-weight: 400;
  color: var(--low);
}
.contact-me button {
  border: none;
  background: none;
  font-weight: 600;
  font-size: 1.25rem;
}
.button-text {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
}

.arrow-right {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 2560px) and (min-width: 1500px) {
}

@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 768px) {
  .oppurtunity {
    padding: 8rem 5rem;
  }
  .oppurtunity_content
  {
    flex: 0.8;
  }
  .oppurtunity_content h1 {
    font-size: 1.75rem;
  }
  .oppurtunity_content p {
    font-size: 1.15rem;
  }
  .contact-me button {
    font-size: 1.15rem;
  }
}

@media screen and (max-width: 550px) {
    .oppurtunity
    {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        gap: 3rem;
        padding: 8rem 3rem;
    }
}

@media screen and (max-width:426px) {
    .oppurtunity
    {
        padding: 8rem 2rem;
    }
    
}

@media screen and (max-width: 400px) {
    .oppurtunity_content h1 {
        font-size: 1.5rem;
      }
      .oppurtunity_content p {
        font-size: 1.1rem;
      }
      .contact-me button {
        font-size: 1.1rem;
      }
}

@media screen and (max-width: 320px) {
    .oppurtunity_content h1 {
        font-size: 1.25rem;
      }
      .oppurtunity_content p {
        font-size: 0.9rem;
      }
      .contact-me button {
        font-size: 0.9rem;
      }
}
