.testimonial {
  background: black;
  font-family: var(--font-family);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 5rem 0rem;
}

.testimonial-heading h1 {
  color: rgb(173, 215, 226);
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  padding-bottom: 3rem;
}

.testimonial-container {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-auto-rows: auto;
  grid-auto-flow: column dense;
}

.testimonial-box {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 2rem;
  border-radius: 12px;
  width: 25rem;
  height: auto;
  gap: 1rem;
}
.person {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.name {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
}
.name img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.name h2 {
  font-size: 1.25rem;
}

.name h3 {
  font-size: 0.75rem;
  font-weight: 400;
  color: rgb(0, 0, 0);
}

@media screen and (max-width: 2560px) and (min-width: 1500px) {
}

@media screen and (max-width: 1300px) {
  .testimonial-container {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(2, minmax(25rem, 1fr));
    grid-auto-rows: auto;
    grid-auto-flow: row dense;
  }
}

@media screen and (max-width: 768px) {
  .testimonial-container {
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-auto-rows: auto;
    grid-auto-flow: row dense;
  }
}

@media screen and (max-width: 550px) {
  .testimonial-container {
    grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  }
  .testimonial-box {
    width: 22rem;
  }
}

@media screen and (max-width: 400px) {
  .testimonial-container {
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  }
  .testimonial-box {
    width: 20rem;
  }
}

@media screen and (max-width: 320px) {
  .testimonial-container {
    grid-template-columns: repeat(auto-fit, minmax(17.5rem, 1fr));
  }
  .testimonial-box {
    width: 17.5rem;
  }
}
