:root {
  --high: white;
  --low: black;
  --mid: rgb(64,123,255);
}
.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 1.25rem 3rem 0.75rem 3rem;
  font-family: var(--font-family);
  background: var(--low);

  position: sticky;
  top: 0rem;
  z-index: 10;
  color: var(--high);
}

.navbar-logo{
  cursor: pointer;
}
.navbar-laptop {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.navbar-laptop-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.navbar-laptop-container a {
  color: var(--high);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.navbar-laptop-container a:hover {
  color: var(--mid);
}

.navbar-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.navbar-button button {
  color: var(--high);
  font-size: 1rem;
  font-weight: 400;
  padding: 0.75rem 1.25rem;
  border: 2px solid var(--high);
  border-radius: 12px;
}

.app__navbar-smallscreen {
  display: none;
}

.app__navbar-smallscreen_overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: var(--low);
  transition: 0.5s ease;

  flex-direction: column;
  z-index: 5;
}

.app__navbar-smallscreen_overlay .overlay__close {
  font-size: 1.5rem;
  color: var(--high);
  cursor: pointer;
  position: absolute;
  top: 3rem;
  right: 3rem;
}

.app__navbar-smallscreen_links
{
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}



.app__navbar-smallscreen_links li{
  margin: 2rem;
  cursor: pointer;
  color: var(--high);
  font-size:2rem;
  text-align: center;

}

.app__navbar-smallscreen_links li:hover{
  color: var(--color-contrast);
}

.navbar-phone-container-links nav a {
  color: var(--high);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

