.PJ__contact {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
  font-family: var(--font-family);
  padding: 8rem 5rem;
}

.contact__heading h1{
  color: var(--low);
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  padding-bottom: 1rem;
}

.contact-links
{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
    font-size: 3rem;
}

@media screen and (max-width:2560px) and (min-width:1500px) {
  
}

@media screen and (max-width:1024px) {
  
}

@media screen and (max-width:768px) {
  .PJ__contact {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width:550px) {
  .contact__heading h1{
    font-size: 1.75rem;
  }
  .contact-links
  {
    font-size: 2.75rem;
  }
}

@media screen and (max-width:400px) {
  .contact__heading h1{
    font-size: 1.5rem;
  }
  .contact-links
  {
    font-size: 2.5rem;
  }
}

@media screen and (max-width:320px) {
  .contact__heading h1{
    font-size: 1.25rem;
  }
  .contact-links
  {
    font-size: 2.25rem;
  }
}
