.work2{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5rem 6rem;
  font-family: var(--font-family);
  gap: 5rem;
}

.check {
  color: var(--color-dark);
  font-size: 3.25rem;
  box-shadow: 5px 5px 10px 0px #888888;
  border-radius: 360px;
  border: 2px solid black;
  margin-bottom: 0.75rem;
}

.work2-data {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.work2-data h1 {
  font-size: 2.75rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
  color: var(--color-dark);
}

.work2-data p {
  font-size: 1.25rem;
  margin: 0;
  color: var(--color-light);
}
.work2-image {
  display: flex;
  flex: 0.25;
}

.work2-image img {
  width: 600px;
  height: auto;
  box-shadow: 10px 10px 5px 0px #888888;
  object-fit: contain;
}

.link
{
  margin-top: 0.75rem;
}
.center_div
{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
}

.center_div, .work2-image a 
{
  cursor: pointer;
}

.linkto
{
  font-size: 0.8rem;
}

@media screen and (max-width: 2560px) and (min-width: 1550px) {
  .work2 {
    padding: 10rem 12rem;
  }
  .check {
    font-size: 6.25rem;
  }
  .work2-data h1 {
    font-size: 5rem;
  }
  .work2-data p {
    font-size: 2.25rem;
  }
  .work2-image img {
    width: 1050px;
  }
}

@media screen and (max-width: 1024px) {
  .work2 {
    padding: 4rem 5rem;
  }
  .check {
    font-size: 2.75rem;
  }
  .work2-data h1 {
    font-size: 1.75rem;
  }
  .work2-data p {
    font-size: 1rem;
  }
  .work2-image img {
    width: 450px;
    height: 300px;
  }
}

@media screen and (max-width: 800px) {
  .work2 {
    flex-direction: column;
    gap: 3rem;
  }

  .tick {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .work2 {
    padding: 4rem 1.5rem;
  }
  .check {
    text-align: center;
  }
  .work2-data {
    align-items: center;
  }
  .work2-data h1,
  .work2-data p {
    text-align: center;
  }
}

@media screen and (max-width:550px) {
  .check {
    font-size: 2.5rem;
  }
  .work2-data h1 {
    font-size: 1.5rem;
  }

  .work2-image img {
    width: 375px;
    height: 250px;
  }
}

@media screen and (max-width: 350px) {
  .check {
    font-size: 2.25rem;
  }
  .work2-data h1 {
    font-size: 1.25rem;
  }
  .work2-data p {
    font-size: 0.85rem;
  }
  .work2-image img
  {
    width: 300px;
    height: 200px;
  }
  
}